import React, { useContext } from "react";
import Image from "gatsby-image";
import {
	StyledFeatureList,
	StyledFeatureItem,
	StyledFeatureContent,
	StyledFeatureContainer,
	StyledStickyContainer,
	StyledFeaturedImageWrapper,
	StyledFeaturedImage,
} from "../../styles";
import { Header } from "../../components";
import { ThemeContext } from "../../contexts";
import { FeatureQuery } from "../../queries";

const Features: React.FC = () => {
	const {
		allMarkdownRemark,
		tasksPreviewLight,
		tasksPreviewDark,
		configPreviewLight,
		configPreviewDark,
	} = FeatureQuery();

	const { isDarkMode } = useContext(ThemeContext);

	const { node } = allMarkdownRemark.edges[0];

	return (
		<StyledFeatureContent>
			<Header node={node} />

			<StyledFeatureContainer>
				<StyledStickyContainer>
					<StyledFeaturedImageWrapper>
						<StyledFeaturedImage>
							<Image
								fluid={
									isDarkMode
										? tasksPreviewDark.childImageSharp.fluid
										: tasksPreviewLight.childImageSharp.fluid
								}
								alt="tasks preview"
							/>
						</StyledFeaturedImage>
						<StyledFeaturedImage>
							<Image
								fluid={
									isDarkMode
										? configPreviewDark.childImageSharp.fluid
										: configPreviewLight.childImageSharp.fluid
								}
								alt="config preview"
							/>
						</StyledFeaturedImage>
					</StyledFeaturedImageWrapper>
				</StyledStickyContainer>

				<StyledFeatureList>
					{node.frontmatter.features?.map((feature, index) => (
						<StyledFeatureItem key={index}>
							<h5>{feature.heading}</h5>
							<p>{feature.description}</p>
						</StyledFeatureItem>
					))}
				</StyledFeatureList>
			</StyledFeatureContainer>
		</StyledFeatureContent>
	);
};

export default Features;
